import React from 'react';
import s from '../popup.module.css'
import close from '../../../images/close.svg';

function AddUserPopup() {
  return (
    <div id='addUserPopup' className={s.popup}>
      <div className={s.popup__content}>
        <img src={close} alt="" className={s.popup__close} />
        <h3 className={s.popup__title}>Редактировать профиль</h3>
        <form id='form-addUser' className={s.popup__form} noValidate name="new">
          <div className="input-container ">
            <input id="name" type="text" minLength="2" maxLength="30" name="name" required
                   className={[s.popup__input, s.popup__input_type_name].join(' ')}
                    placeholder="Имя" />
          </div>
          <div className="input-container ">
            <input id="job" type="text" minLength="2" maxLength="30" name="job" required
                   className={[s.popup__input, s.popup__input_type_job].join(' ')}
                    placeholder="О себе" />
          </div>
          <button id="submit" type='submit' className={[s.button, s.popup__button, s.popup__button_user].join(' ')}>Сохранить</button>
        </form>
      </div>

    </div>
  );
}

export default AddUserPopup;
